<template>
  <div class="wiki-outline-warp">
    <div class="wiki-outline-warp-nav">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>{{type_name}}</el-breadcrumb-item>
        <el-breadcrumb-item>{{book_name}}</el-breadcrumb-item>
        <el-breadcrumb-item>{{article_title}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="wiki-outline">
      <outlineLeft :outlineList="outlineList" @getAricitleTitle='getAricitleTitle'></outlineLeft>
      <outlineCenter :outlineList="outlineList"></outlineCenter>
      <outlineRight></outlineRight>
    </div>
  </div>
</template>

<script>
import outlineLeft from "@/components/Outline/outlineLeft";
import outlineCenter from "@/components/Outline/outlineCenter";
import outlineRight from "@/components/Outline/outlineRight";
import * as ajax from "@/api";

export default {
  components: {
    outlineLeft,
    outlineCenter,
    outlineRight
  },
  data() {
    return {
      outlineList: [],
      default_id: 1,
      type_name:'',
      book_name:'',
      article_title:''
    };
  },
  created() {
    ajax.getOutlineList(this.$route.query.id).then(resp => {
      if (resp.data.code) {
        this.outlineList = resp.data.data.article_info;
        this.default_id = resp.data.data.default_doc_id;
      }
    })
    this.type_name = this.$route.query.type_name
    this.book_name = this.$route.query.book_name
  },
  methods:{
      getAricitleTitle(data){
          this.article_title= data
      }
  }
};
</script>

<style lang='scss'>
.wiki-outline-warp {
  width: 100%;
  height: 100%;
  .wiki-outline {
    width: 1280px;
    margin: 0 auto;
    display: flex;
    height: 100%;
  }
  &-nav {
    width: 100%;
    .el-breadcrumb {
      width: 1280px;
      margin: 0 auto;
      height: 90px;
      line-height: 90px;
      padding-left: 20px;
      font-size: 16px;
      div {
        height: 90px;
        border-bottom: 1px solid #e1e4e5;
      }
      .outline-main-center-modify-arcitle {
        color: #2980b9;
        float: right;
        font-size: 16px;
        padding-right: 20px;
      }
    }
  }
}
</style>
