<template>
  <div class="wiki-outline-main-left">
    <div class="wiki-outline-main-left-main">
      <el-tree
        :data="data"
        :props="defaultProps"
        accordion
        @node-click="handleNodeClick"
        :default-expanded-keys= "['1']"
        >
      </el-tree>
    </div>
  </div>
</template>

<script>
import * as ajax from "@/api";
import { mapMutations } from "vuex";

export default {
  data() {
    return {
      title: "",
      defaultArr:[],
      data: [],
      defaultProps: {
        children: "children",
        label: "label",
      }
    };
  },
  props: ["outlineList", "default"],
  methods: {
    toggleAritcle(id, index, item) {
      this.$emit("getAricitleTitle", item.title);
      ajax.getArticleDetail(id).then(resp => {
        this.getarticle(resp.data.data.article_info.content); //获取文章内容
      });
    },
    ...mapMutations(["getarticle"]),
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    handleNodeClick(filterNode){
      console.log(filterNode)
      if(filterNode.book_name){
        this.bus.$emit('toggleArictleList',filterNode.id)
      }
    }
  },
  created() {
    this.title = this.$route.query.book_name;
    ajax.getHomeList().then(resp=>{
      if(resp.data.code){
        this.data2 = resp.data.data.all_type_books
        let data = resp.data.data.all_type_books
        data.forEach(element => {
          element["value"] = element["type"]["id"];
          element["label"] = element["type"]["type_name"];
          element["children"] = element["books"];
          element["children"].forEach(item => {
            item["value"] = item["id"];
            item["label"] = item["book_name"];
          });
        });
        this.data = data
        this.defaultArr = [1]
      }
    })

  },
  watch: {
    filterText(val) {
      this.$refs.tree2.filter(val);
    }
  }
};
</script>

<style lang='scss'>
.wiki-outline-main-left {
  width: 200px;
  min-height: 100%;
  background-color: #f6f6f6;
  .el-row {
    .el-col-12 {
      width: 100%;
    }
  }
}
</style>
