<template>
  <div class="wiki-outline-main-right">
        <h3>贡献者排行版</h3>
       <ol>
           <li 
            v-for="(item,index) in list"
            :key="item.id"
            >第{{index+1}}名：{{item.username}}</li>
       </ol>
  </div>
</template>

<script>
import * as ajax from '@/api'
export default {
   created(){
       this.bus.$on('toggleArictleList',id=>{
           ajax.getContributorList(id).then(resp=>{
               console.log(resp)
               if(resp.data.code){
                   this.list = resp.data.data.user_info
               }
           })
       })
       ajax.getContributorList(this.$route.query.id).then(resp=>{
            console.log(resp)
            if(resp.data.code){
                this.list = resp.data.data.user_info
            }
        })
   },
   data(){
       return{
           list:[]
       }
   },
   methods:{
   }
}
</script>

<style lang='scss'>
    .wiki-outline-main-right{
        padding-top: 150px;
        padding-left: 20px;
        ol{
            width: 180px;
            list-style: none;
        }
    }
</style>
