<template>
  <div class="wiki-outline-center-task">
    <h3>任务:</h3>
    <el-collapse v-model="activeNames" @change="handleChange">
      <el-collapse-item
      v-for="(item,index) in list"
      :key="index" 
      :title="item.title" 
      :name="index">
        <div>所属文章：{{item.book_name}}</div>
        <div>任务技术点: {{item.type_name}}</div>
        <div>目前状态: {{item.status}}</div>
        <div>具体内容：{{item.content}}</div>
        <div>发布时间: {{item.create_time}}</div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import * as ajax from "@/api";

export default {
  created() {
    ajax.getTaskList().then(resp => {
      console.log(resp);
      if(resp.data.code){
        this.list = resp.data.data.task_list
      }
    });
  },
  data() {
    return {
      activeNames: ["1"],
      list:[]
    };
  },
  methods: {
    handleChange(val) {
    //   console.log(val);
    }
  }
};
</script>

<style lang='scss'>
.wiki-outline-center-task{
    margin-top: 60px;
}
</style>
