<template>
  <div class="bc-outline-main-center">
    <h3>文章列表:</h3>
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column fixed prop="pub_date" label="发布日期" width="200"></el-table-column>
      <el-table-column prop="name" label="发布人" width="120"></el-table-column>
      <el-table-column prop="title" label="文章标题" width="220"></el-table-column>
      <el-table-column fixed="right" label="操作" width="auto">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <outilineCenterTask></outilineCenterTask>
  </div>
</template>

<script>
import * as ajax from '@/api'
import outilineCenterTask from '@/components/Outline/outilineCenterTask'

export default {
  data() {
    return {
      html: [],
      tableData: [],
      data: ""
    };
  },
  methods: {
    handleClick(row) {
      console.log(row)
      this.$router.push({path:'/article', query:{row:row}}) //查看文章
    },
  },
  props: ["outlineList"],
  watch: {
    arcitle(newval, old) {
      this.html = newval;
    }
  },
  created() {
    this.bus.$on('toggleArictleList',id=>{
      ajax.getOutlineList(id).then(resp=>{
        if(resp.data.code){
          resp.data.data.article_info.forEach(element => {
            element['name'] = element['part_user_info']['article_user_username']
          });
          this.tableData = resp.data.data.article_info
        }else{
          this.tableData = []
        }
      })
    })
    ajax.getOutlineList(this.$route.query.id).then(resp=>{
        if(resp.data.code){
          resp.data.data.article_info.forEach(element => {
          element['name'] = element['part_user_info']['article_user_username']
        });
        this.tableData = resp.data.data.article_info
      }
    })
  },
  components:{
    outilineCenterTask
  }

};
</script>

<style lang='scss'>
.bc-outline-main-center {
  flex: 1;
  min-width: 730px;
  margin-left: 20px;
  height: auto;
  .bc-outline-main-center-header-people-data {
    display: none;
    padding-left: 20px;
    height: 50px;
    img {
      width: 36px;
      height: 36px;
      margin-right: 10px;
    }
  }

  .editor-container {
    min-height: 400px;
    position: relative;
    h1 {
      position: absolute;
      padding-left: 13px;
    }
    .simplemde-container {
      // height: auto !important;
      min-height: 405px;
      width: 100%;
      #contentEditor {
        display: none;
      }
    }
  }
}
</style>
